.staticBG {
  background-color: rgb(27, 26, 26);
  width: 100vw;
  height: 100vh;
}

.Homepage {
  background-color: rgb(31, 31, 31);
  text-align: center;
}

.banner {
  margin-top: 20px;
  margin-left: 2%;
  margin-right: 2%;
  margin-bottom: 20px;
  position: relative;
}

.overlay {
  position: absolute;
  bottom: 0px;
  padding: 5px;
  background-color: black;
  opacity: 75%;
  border: 2px solid gray;
  width: 100%;
  height: 14%;
}

.overlayText {
  position: absolute;
  bottom: 2%;
  left: 10px;
  padding: 5px;
  font-family: Roboto-Black;
  font-size: 1.5vw;
  color: white;
}

.bannerimg {
  object-fit: contain;
  height: auto;
  width: 100%;
  border: 2px solid black;
}

.navbar {
  width: 100%;
}

.bg-nav {
  background-color: rgb(143, 143, 143);
}

.logobox {
  width: 75%;
  float: left;
}

.logo {
  object-fit: contain;
  height: auto;
  width: 30%;
}

.loginbox {
  width: 25%;
  float: right;
}

.App-header {
  padding: 20px;
  position: relative;
  width: 100%;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.pagePlaceholder {
  color:white;
  text-align: center;
}

.welcome {
  color:white;
  float: left;
  padding-right: 10px;
}

.form-box {
  color:white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.test {
  text-align: center;
}